.app_wrapper {
    width: 1200px;
    max-width: 100%;
    margin: 9px auto;
    display: grid;

    grid-template-areas: 
    "h h"
    "n c";

    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
}
.app_wrapper_content {
    grid-area: c;
    background-color: blue; 
}